// ** React Imports
import { Fragment, useState, useEffect, memo } from 'react'
import UiLoader from '@components/ui-loader'
// ** Table Columns
import logo from '../../../../assets/images/boomer11sports/Cricket-yellow.svg'
import AppCollapse from '@components/app-collapse'
// ** Store & Actions
import { getbannerlistType, bannertoggle, viewbanner  } from '../../store/action' //cricketdeletebanner
import { useSelector, useDispatch } from 'react-redux'
import {useHistory} from 'react-router-dom'
// import withReactContent from 'sweetalert2-react-content'
// import Swal from 'sweetalert2' 
// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'

// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown, Edit, Filter } from 'react-feather'
import DataTable from 'react-data-table-component'
import { Card, Input, Label, Row, Col, CustomInput } from 'reactstrap'
const DataTableTransaction = () => {
  // const MySwal = withReactContent(Swal)
  // ** Store Vars
const history = useHistory()
  const dispatch = useDispatch()
  const store = useSelector(state => state.banner)
  
  console.log(store.banner_list)
  // ** States

  const [currentPage, setCurrentPage] = useState(1)
  const [dataPerPage, setDataPerPage] = useState(10)
  const [searchValue, setSearchValue] = useState('')
  const [filteredData, setFilteredData] = useState([])
  const [status, setStatus] = useState(false)
  
  const SwitchClick = (row) => {  
    setStatus(!status)         
    dispatch(bannertoggle(row, status))   
  }
  
  useEffect(() => {  
    dispatch(getbannerlistType(currentPage, dataPerPage))
  }, [currentPage, store.banner_status])

  useEffect(() => {
    setCurrentPage(1)
    dispatch(getbannerlistType(1, dataPerPage))
  }, [dataPerPage])
    
  // ** Get data on mount
  // const handleConfirmText = (id) => {
  //   return MySwal.fire({
  //     title: 'Are you sure?',
  //     text: "You won't be able to revert this!",
  //     icon: 'warning',
  //     showCancelButton: true,
  //     confirmButtonText: 'Yes, delete it!',
  //     customClass: {
  //       confirmButton: 'btn btn-primary',
  //       cancelButton: 'btn btn-outline-danger ml-1'
  //     },
  //     buttonsStyling: false
  //   }).then(function (result) {
  //     if (result.value) {
  //       dispatch(cricketdeletebanner(id))
  //       console.log(id)
  //       MySwal.fire({
  //         icon: 'success',
  //         title: 'Deleted!',
  //         text: 'Your file has been deleted.',
  //         customClass: {
  //           confirmButton: 'btn btn-success'
  //         }
  //       })
  //     }
  //   })
  // }
  
  const editHandler = (row) => {
   
    history.push({pathname:"/banner/edit"})
    dispatch(viewbanner(row.id))
    
  }
  
  console.log()
  console.log(store)

  const customStyles = {
    rows: {
      style: {
        minHeight: '102px' // override the row height
      }
    },
    headCells: {
      style: {
        marginLeft:'20px',
        paddingLeft: '8px', // override the cell padding for head cells
        paddingRight: '8px'
      }
    },
    cells: {
      style: {
        margin:'30px 10px',
        paddingLeft: '8px', // override the cell padding for data cells
        paddingRight: '8px'
      }
    }
  }

  const columns = [
    {
      name: '#',
      minWidth: '10px',
      selector: '#',
      cell: (row, pos) => (`#${store.banner_list.from + pos}`)
    }, 
    {
      name: 'Banner',
      id:'banner',
      minWidth: '200px',
      dense: true,
      selector: 'banner_image',
      cell: (row) => (
        <img src={row.assets_url} style={{ height: '80px', with: '180px'}} />
      )
    },
    {
      name: 'Sports',
      minWidth: '20px',
      selector: 'sports',    
      cell: () => (
        <img src={logo} style={{ height: '40px', width: '40px', marginLeft:'30px'}} />
        // <img src={require('../../../../assets/images/boomer11sports/Cricket-yellow.svg')} style={{ height: '300', width: '100px'}} />
      )
    },  
    {
      name: 'Position',
      minWidth: '20px',
      selector: 'position',
      sortable: true,
      cell: (row) => row.position
    },
    {
      name: 'Screen',
      minWidth: '20px',
      selector: 'type',
      sortable: true,
      cell: (row) => row.type
    },
    {
      name: 'Status',
      minWidth: '138px',
      selector: 'active',
      sortable: true,
      cell: (row) => (
        <div className="column-action">
          {            
            (
              <CustomInput 
            className='custom-control-success'
            type='switch'
            id={`success${row.id}`}
            name='success'
            onChange={() => SwitchClick(row.id)}
            inline
            defaultChecked = {row.active !== 0}
          />
            ) 
          }                 
        </div>
      )
    }, 
    {
      name: 'Action',
      minWidth: '138px',
      selector: 'active',
      cell: (row) => (
        <div className="column-action">
          {/* <a href={`/walls/edit/${row.feed_id}`}> */}
          <Edit width="17" className="cursor-pointer" title="Edit Banner" onClick={() => editHandler(row)} />
          {/* </a> */}
          {/* <Trash2 width="17" className="ml-2 cursor-pointer" title="Add Feed" onClick={() => handleConfirmText(row.id)} /> */}
        </div>
      )
    }    
  ]
  // ** Function to handle filter
  const handleFilter = e => {
    const value = e.target.value
    let updatedData = []
    setSearchValue(value)
    if (value.length) {
      updatedData = store.banner_list.data.filter(item => {        
        console.log(item)
       const startsWith =
          item.type?.toLowerCase().startsWith(value.toLowerCase()) ||
           item.position?.toString().toLowerCase().startsWith(value.toLowerCase()) 
          // item.debit_credit?.toLowerCase().startsWith(value.toLowerCase()) ||
          // date?.toString().toLowerCase().startsWith(value.toLowerCase()) ||
          // item.transaction_type?.toLowerCase().startsWith(value.toLowerCase()) ||
          // item.txn_id.toLowerCase()?.startsWith(value.toLowerCase())

        const includes =
          item.type?.toLowerCase().includes(value.toLowerCase()) ||
           item.position?.toString().toLowerCase().includes(value.toLowerCase()) 
          // item.debit_credit?.toLowerCase().includes(value.toLowerCase()) ||
          // date?.toString().toLowerCase().includes(value.toLowerCase()) ||
          // item.transaction_type?.toLowerCase().includes(value.toLowerCase()) ||
          // item.txn_id?.toLowerCase().includes(value.toLowerCase())

        if (startsWith) {
          return startsWith
        } else if (!startsWith && includes) {
          return includes
        } else return null
      })
      setFilteredData(updatedData)
    }
  }

  // ** Function to handle Pagination and get data

  const handlePagination = page => {
    setCurrentPage(page.selected + 1)
  }

  // ** Function to handle per page

  // ** Custom Pagination
  const CustomPagination = () => {

     const count = store.banner_list.last_page
    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        breakLabel='...'
        pageCount={count}
        marginPagesDisplayed={2}
        pageRangeDisplayed={2}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        breakClassName='page-item'
        breakLinkClassName='page-link'
        containerClassName={
          'pagination react-paginate separated-pagination pagination-sm justify-content-end pr-1 mt-1'
        }
      />
    )
  }
console.log(store.banner_list.data)
  // ** Table data to render
  const dataToRender = () => {
    if (store.banner_list.data?.length > 0) {
      return searchValue.length ? filteredData : store.banner_list.data
    } else {
      return []
    }
  }

  if (store.isloading) {   
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }

  const data = [
    {
      title: <div><Filter size={18}/> Filter</div>,
      content: (
        <Row xl={12}>
          <Col
          xl="12"
          className="d-flex align-items-sm-center justify-content justify-content-start flex-lg-nowrap flex-wrap flex-sm-row flex-column pr-lg-1 p-0 mt-lg-0 mt-1"
        >
          <div className="d-flex align-items-center mb-sm-0 mb-1 mr-1 ml-1">
            <Label className="mb-0 mr-1" for="search-invoice">
              Search:
            </Label>
            <Input
              className='dataTable-filter'
              type='text'
              bsSize='sm'
              id='search-input'
              value={searchValue}
              onChange={handleFilter}
              placeholder='search...'
            />
          </div>
          
       </Col>
        </Row>
      )
    }
  ]
 
  return (
    <Fragment>
      <Card>
      <AppCollapse data={data} type='border' accordion />
      </Card>
     
      <Card>
       <Row className='mx-0 mt-1 mb-50'>
          <Col sm='6'>
            <div className='d-flex align-items-center'>
              <Label for='sort-select'>show</Label>
              <Input
                className='dataTable-select'
                type='select'
                id='sort-select'
                value={dataPerPage}
                onChange={e => setDataPerPage(e.target.value)}
              >
                <option value={10}>10</option>
                <option value={50}>50</option>
                <option value={100}>100</option>
              </Input>
              <Label for='sort-select'>entries</Label>
            </div>
          </Col>
        </Row>
        <DataTable
          noHeader
          // pagination
          paginationServer
          customStyles={customStyles}
          className='react-dataTable'
          columns={columns}
          sortIcon={<ChevronDown size={10} />}
          paginationComponent={CustomPagination}
          data={dataToRender()}
        />
      </Card>
      {
        store.banner_list.data?.length === 0 ? '' : <CustomPagination />
      } 
    </Fragment>
  )
}

export default memo(DataTableTransaction)
